img {
  width: 100%;
}

.main-banner {
  color: yellow;
}

.landing {
  position: relative;
  background: url("./media/bg2.1.jpg") no-repeat;
  background-size: cover;
  background-position: center;
  height: 100vh;
  margin-top: -24px;
  margin-bottom: -50px;
}

.landing-inner {
  padding-top: 80px;
}

.dark-overlay {
  background-color: rgba(0, 0, 0, 0.68);
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

#landing-con {
  margin-top: 120px;
}

.card-form {
  opacity: 0.9;
}

.latest-profiles-img {
  width: 40px;
  height: 40px;
}

.form-control::-webkit-input-placeholder {
  color: #bbb !important;
}

.form-control:-ms-input-placeholder {
  color: #bbb !important;
}

.form-control::-ms-input-placeholder {
  color: #bbb !important;
}

.form-control::placeholder {
  color: #bbb !important;
}

.footer {
  bottom: 0;
  width: 100%;
  height: 90px;
  /* Set the fixed height of the footer here */
  line-height: 50px;
}

.btn-light:hover {
  background-color: rgb(226, 226, 226);
  border-color: rgb(228, 228, 228);
}

.form-imp {
  width: 400px;
  margin: auto;
}

.pro-card {
  margin: auto;
}

.post-form {
  margin: auto;
  -webkit-box-shadow: 0px 0px 20px #bbb;
          box-shadow: 0px 0px 20px #bbb;
  padding: 0px;
}

.btn-cover {
  width: 140px;
  text-align: center;
}

label {
  margin: auto;
}



